// app global css in SCSS form
a {
  text-decoration: none;
  color: #fff;
}

html {
  background: url('https://cdn.wallpapersafari.com/94/67/Q0lqza.jpeg');
  background-size: cover;
  color: rgba(255,255,255, 0.8);
}

.q-card {
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.2);
  -webkit-backdrop-filter: blur(40px);
          backdrop-filter: blur(40px);
  border: solid 2px transparent;
  background-clip: padding-box;
}

.q-btn-group {
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.2);
  -webkit-backdrop-filter: blur(40px);
          backdrop-filter: blur(40px);
  border: solid 2px transparent;
  background-clip: padding-box;
}

.q-drawer {
  margin: 2em;
  padding: 2em;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.2);
  -webkit-backdrop-filter: blur(40px);
          backdrop-filter: blur(40px);
  border: solid 2px transparent;
  background-clip: padding-box;

  a:hover {
    color: orange;
  }
}

.names {
  position: fixed;
  bottom: 0;
  margin-bottom: 3em;
}

.namess {
  position: fixed;
  bottom: 0;
  margin-bottom: 1em;
}